nav {
    position: fixed;
    width: 100vw;
    padding: 10px;
    background-color: white;
    box-shadow: -5px 9px 10px rgba(199, 199, 199, 0.6);
    z-index: 10;
    margin-bottom: 30px;
}

nav ul {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.contactUs-navTab {
    padding-right: 70px;
}

.site-name {
    display: flex;
    justify-content: center;
    align-items: center;
}

nav li {
    height: 52px;
}

nav a {
    height: 100%;
    padding: 0 20px;
    font-size: 17px;
    text-decoration: none;
    display: flex;
    align-items: center;
    color: black;
}

nav a:hover {
    color: #E49B0F;
}

nav li:first-child {
    margin-right: auto;
}

.sidebar {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 30%;
    background-color: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(12px);
    box-shadow: -10px 0 10px rgba(0, 0, 0, 0.1);
    list-style: none;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    z-index: 10;
}

.close-menu {
    padding-top: 10px;
}

.sidebar li {
    width: 100%;
}

.sidebar a {
    width: 100%;
}

.menu-button {
    display: none;
}

.site-logo {
    display: flex;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    margin-left: 40px;
    margin-bottom: 5px;
}

@media(max-width: 800px) {

    .hideOnMobile {
        display: none;
    }

    .menu-button {
        display: block;
    }
}

@media (max-width: 600px) {

    nav a {
        padding: 0 15px;
        font-size: 14px;
        letter-spacing: -0.3px;
    }

    .site-logo {
        width: 1.8rem;
        height: 1.8rem;
        margin-top: 2.5px;
        margin-left: 20px;
    }
}

@media(max-width: 400px) {

    .sidebar {
        width: 50%;
        height: 100%;
    }

    .site-logo {
        margin-left: 10px;
    }

    .menu-button {
        margin-right: 10px;
    }
}